.hero, .profile {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 111px 0 30px;
  &__image {
    object-fit: cover;
    height: 135px;
    margin-bottom: 10px;
  }
  &__header {
    font-size: 48px;
    margin-bottom: 5px;
  }
  &__text {
    font-size: 24px;
  }
}
.hero__image_gif {
  filter: brightness(1.5);
}
.profile {
  padding-bottom: 0;
  &__image {
    border-radius: 100%;
    width: 150px;
    height: 150px;
    background: #fff;
  }
}