.actual {
  &__list {
    display: flex;
    position: relative;
    .arrow {
      z-index: 2;
      content: '';
      position: absolute;
      top:0;
      left:-290px;
      width: 280px;
      height: 160px;
      border: none;
    }
    .left-arrow {
      background: linear-gradient(-2700deg, #000000 0%, #000000 0.01%, rgba(0, 0, 0, 0.8) 109.36%);
      img {
        transform: rotate(180deg);
        margin-left: 180px;
      }
    }
    .right-arrow {
      background: linear-gradient(270deg, #000000 0%, #000000 0.01%, rgba(0, 0, 0, 0.8) 109.36%);
      left: auto;
      right: -290px;
      img {
        margin-right: 180px;
      }
    }
  }
}