.register, .login {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 25px 0 100px;
  &__title {
    font-size: 32px;
  }
  &__form {
    &__input {
      font-size: 24px;
      display: block;
      width: 440px;
      padding: 8px 20px;
      margin: 10px 0 20px;
      background: transparent;
      border: none;
      border-bottom: 1px solid #FFFFFF;
    }
  }
  &__text {
    font-size: 24px;
    a {
      text-decoration: underline;
    }
  }
  &__button_primary {
    margin: 25px 0 15px;
  }
}